.dropdown-menu {
  opacity: 0;
  list-style: none;
  right: 0;
  visibility: hidden;
  position: absolute;
}
.dropdown-menu__show {
  display: flex;
  flex-direction: column;
  transition: visibility 0s 0s, opacity 0.6s ease;
  position: absolute;
  visibility: visible;
  top: 20px;
  opacity: 1;
  bottom: 0;
  right: 0;
  left: 0;
  padding: 30px;
  width: max-content;
