.form-delivery__invalid {
  display: none;
  color: #D80909;
  font-weight: 600;
  font-size: 13px;
  line-height: 16px;
  padding-top: 8px;
}
.form-delivery__invalid--show {
  display: block;
  position: absolute;
  bottom: -16px;
}
.form-delivery__validation .form-delivery__invalid {
  border: 1px solid #D80909;
  transition: 0.5s ease;
}
.form-delivery__checkbox {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.form-delivery__checkbox + .form-delivery__consent::before {
  content: "";
  position: absolute;
  width: 25px;
  height: 25px;
  background-color: #F9F9F9;
  border: 1px solid #D5D5D5;
  display: block;
  top: -5px;
}
.form-delivery__checkbox--validation-red + .form-delivery__consent::before {
  border-color: #D80909;
}
.form-delivery__checkbox:checked + .form-delivery__consent::before {
  content: url("../img/checkbox.svg");
  position: absolute;
  width: 25px;
  height: 25px;
  display: block;
}
